/*************************
 * Global
 *************************/
body, .s2k-shortcut-table {
  font-family: Arial, sans-serif;
  font-size: 12px;
}

a {
  color: #004b91;
  text-decoration: none;
}

a:hover {
  color: #e47911;
  text-decoration: underline;
}

/*************************
 * Classes
 *************************/
.s2k-setup-header {
  width: 297px;
  margin: 0 auto;
}

.s2k-setup-banner {
  width: 297px;
  height: 81px;
  /*noinspection CssUnknownTarget*/
  background: url("/images/s2k-setup-sprite.png") -396px -329px;
}

.s2k-setup-box {
  width: 400px;  
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #cdcdcd;
  border-radius: 15px;
}

.s2k-setup-section-line {
  width: 250px;
  height: 1px;
  margin: 15px auto;
    /*noinspection CssUnknownTarget*/
    background: url("/images/s2k-setup-sprite.png") -258px 0;
}

.s2k-setup-section {
}

.s2k-section-left {
  float: left;
}

.s2k-section-right {
  float: right;
}

.s2k-section-divider {
  float: left;
  width: 1px;
  margin-top: -10px;
  margin-left: 15px;
  margin-right: 15px; 
  background-color: #cdcdcd;
}

.s2k-setup-section-header h1 {
  font-size: 18px;
  margin: 0;
  color: #e47911;
}

.s2k-setup-section-header h2 {
  font-size: 12px;
}

.s2k-setup-section-detail input {
  width: 230px;
  height: 2.5em;
  border: 1px solid #CACACA;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
  background-color: #fff;
}

.s2k-setup-section-detail input:focus {
  border: 1px solid #e47911;
  background-color: #e3e3e3;
  border-radius: 5px;
  outline: 0;
}

.s2k-shortcut-table {
  margin: 0 auto;
}

.s2k-shortcut-label {
  text-align: right;
  font-weight: bold;
}

.s2k-shortcut-spacer {
  padding: 0 10px;
}

.s2k-delivery-box {
  margin-top: 20px;
  text-align: center;
  font-size: 14px;
}

.s2k-legal-box {
  margin-top: 50px;
  font-size: 14px;
  text-align: center;
}

.s2k-legal-box span {
  margin: 0 10px;
}